.service-card {
    position: relative;
    display: flex;
    align-items: flex-end;
    min-height: rem(149);
    padding: rem(16) rem(13);
    border: rem(2) solid;
    border-image-source: linear-gradient(
        124.81deg,
        #000000 -36.41%,
        #656669 117.15%
    );
    border-image-slice: 1;
    border-width: rem(2);

    @media (min-width: 768px) {
        min-height: rem(194);
        padding: rem(20);
    }

    @media (min-width: 1024px) {
        min-height: rem(240);
        padding: 0 rem(24) rem(24);
    }

    @media (min-width: 1600px) {
        min-height: rem(322);
    }

    @media (min-width: 1920px) {
        min-height: rem(387);
        padding: 0 rem(48) rem(48);
    }

    &__number {
        position: absolute;
        top: -65%;
        right: 0;
        font-size: rem(130);
        line-height: 170%;
        color: rgba(235, 235, 235, 0.1);
        opacity: 0.3;

        @media (min-width: 600px) {
            top: -75%;
        }

        @media (min-width: 768px) {
            font-size: rem(170);
        }

        @media (min-width: 1024px) {
            right: -10%;
            font-size: rem(210);
        }

        @media (min-width: 1600px) {
            top: -65%;
            right: -15%;
            font-size: rem(300);
        }

        @media (min-width: 1920px) {
            top: -60%;
            font-size: rem(350);
        }
    }

    &__text {
        font-size: rem(10);
        line-height: rem(13);
        color: #656669;

        @media (min-width: 768px) {
            font-size: rem(12);
            line-height: rem(16);
        }

        @media (min-width: 1024px) {
            font-size: rem(13);
            line-height: rem(17);
        }

        @media (min-width: 1600px) {
            font-size: rem(17);
            line-height: rem(23);
        }

        @media (min-width: 1920px) {
            font-size: rem(19);
            line-height: rem(26);
        }
    }
}
